import { CookieService } from './../../../shared/services/cookie.service';
import { Component, OnInit, AfterViewInit, ViewEncapsulation, OnDestroy } from '@angular/core';
import { SubDomain } from '../../../shared/interfaces/subdomain.interface';
import { CompanyService } from '../../../shared/services/company.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Script } from '../../../shared/services/script.service';
import { FeatureAuthService } from '../../../shared/services/feature-access.service';
import { SubDomainService } from '../../../shared/services/subdomain.service';
import { MarketingService } from "../../../shared/services/marketing.service";
import { Title } from '@angular/platform-browser';
import { MembershipService } from "../../../shared/services/membership.service";
import { Subscriptions } from "../../../shared/models/currentPlan";
import { CountdownPromoService } from "../../../shared/services/countdown-promo.service";
declare var jQuery: any;
declare var moment: any;
declare var window: any;

@Component({
  selector: 'og-analytics',
  templateUrl: './analytics.component.html',
  styleUrls: [
    './assets/css/daterangepicker.css',
    './assets/css/analytics.component.css'
  ],
  encapsulation: ViewEncapsulation.None,
})

export class AnalyticsComponent implements OnInit, AfterViewInit, OnDestroy {
  subDomain: SubDomain;
  company_id: String;
  live_calculators: any = [];
  analytic_component: String = '';
  calc_id: String;
  calc_name: String;
  public activeSince: any;
  public isActive: any;
  public calculator: any;
  hellobarMessage: string = '';

  isApp: boolean = true;
  sample_analytics_deatils: any;

  constructor(public companyService: CompanyService,
    public _router: Router,
    public _script: Script,
    public _marketingService: MarketingService,
    public titleService: Title,
    public _featureAuthService: FeatureAuthService,
    public _subDomainService: SubDomainService,
    private _membershipService: MembershipService,
    private countdownPromoService: CountdownPromoService,
    public _cookieService: CookieService,
    public _activateRoute: ActivatedRoute
  ) {

    this.titleService.setTitle("Outgrow Home");
    this.company_id = _subDomainService.subDomain.company_id;
    this._marketingService.initGTM()
      .then(data => this._marketingService.identifyUser())
      .catch(err => console.log(err));
  }

  ngAfterViewInit() {
    this._script.load('highcharts', 'gCharts', 'jqueryUI', 'slimScroll', 'raphael', 'morrisCharts', 'datatables', 'daterangepicker', 'moment')
      .then((data) => {
        //console.log('Scripts Loaded', data);
        window.loadGoogleCharts();
        this.analytic_component = "overview";
      })
      .catch((error) => {
        //any error
      });
    let interval = setInterval(function () {
      if (window.Intercom) {
        window.Intercom('update', { 'app_current_page': 'analytics' });
        window.Intercom('update', { 'app_current_page_url': window.location.href });
        clearInterval(interval);
      }
    }, 1000);
  }

  ngOnInit() {
    this.sample_analytics_deatils = this._activateRoute.snapshot.data.sample_calc;
    this.hellobarMessage = this._cookieService.readCookie('hellobar') ? JSON.parse(this._cookieService.readCookie('hellobar')) : null;
    let self = this;
    //Code
    this.companyService.getLiveCompanyProjects(this.company_id)
      .subscribe(
        (response: any) => {
          if (response.length) {
            self.calculator = response[0];
            this.live_calculators = response;
            this.calc_id = response[0]._id;
            this.calc_name = response[0].name;
            this.activeSince = moment(response[0].createdAt).fromNow().replace('ago', '').trim();
            this.isActive = response[0].mode == 'PUBLIC';
            setTimeout(() => this._featureAuthService.reInitUserDetails.next(true), 500);
          } else {
            this.calc_id = 'null';
            jQuery('body').addClass('hnjjkads');
            this.onCalcSelect(this.sample_analytics_deatils);
          }
        },
        (error: any) => {
          console.log(error);
        }
      );

    let cookie = this._cookieService.readCookie('storage');
    let storage = cookie != null ? JSON.parse(cookie) : '';
    if (storage && storage.token) {
      this._membershipService.getPlanSubscription()
        .subscribe(
          (success: any) => {
            let subscription = new Subscriptions(success.currentplan.subscription);
            switch (subscription.status) {
              case 'in_trial':
                let difference;
                this.countdownPromoService.subscription = subscription;
                let trialEnd = moment.unix(success.currentplan.subscription.trial_end);
                this.countdownPromoService.setCountdownTimer(trialEnd);
                if (this.countdownPromoService.trialEnd) {
                  difference = moment.duration(this.countdownPromoService.trialEnd.diff(moment(new Date())));
                }
                // if (difference.asDays() <= 4 && difference.asDays() >= 0) {
                //this.showPromo = true;
                //}
                break;
            }
          }
        );
    }

    // if (!this._featureAuthService.features.isLoaded) {
    //   this._featureAuthService.getAllFeatureAccess().subscribe((result) => {
    //     this._featureAuthService.features = new FeatureAccess(result);
    //   })
    // }
    this._featureAuthService.getFeatures().subscribe(f => {
      console.log("Innn Analytics", f, this._featureAuthService.features);
    })
  }

  onAnalyticTypeSelect(type: String) {
    let isTrafficAvail = this._featureAuthService.features.analytics.traffic_details;
    let isFunnelAvail = this._featureAuthService.features.analytics.funnel;
    if ((type == 'funnel' && isFunnelAvail) || (type == 'overview' && true) || (type == 'details' && true) || (type == 'traffic' && isTrafficAvail)) {
      this.analytic_component = type;
    }
    else {
      if (type == 'funnel')
        this._featureAuthService.setSelectedFeature('analytics', 'funnel');
      else
        this._featureAuthService.setSelectedFeature('analytics', 'traffic_details');
      jQuery('.analytics').addClass('activegreen limited-label');
      jQuery('#premiumModal').modal('show');
      jQuery('.modal-backdrop').insertAfter('#premiumModal');
    }
  }

  onCalcSelect(calc: any) {
    this.isApp = true;
    if (calc._id == this.sample_analytics_deatils._id) {
      this.calc_id = this.sample_analytics_deatils._id;
      this.calc_name = this._subDomainService.sample_analytics_name;
      this.calculator = this.sample_analytics_deatils;
      this.activeSince = moment(calc.createdAt).fromNow().replace('ago', '').trim();
      this.isActive = calc.mode == 'PUBLIC';
    } else if (calc == 'all') {
      this.isApp = false;
      this.calculator = this.live_calculators;
      // console.log('Calc ids:::: ', this.calc_id);
      this.calc_id = String(this.live_calculators.map(d => d._id));
      // console.log('Calc ids:::: ', this.calc_id);
      this.calc_name = 'All Experiences';

      jQuery('#user_overview').removeClass('active');
      jQuery('#user_funnel').removeClass('active');

      jQuery('#user_detils_analytics_anchor')[0].click();
    } else {
      this.calculator = calc;
      this.calc_id = calc._id;
      this.calc_name = calc.name;
      this.activeSince = moment(calc.createdAt).fromNow().replace('ago', '').trim();
      this.isActive = calc.mode == 'PUBLIC';
    }
    jQuery('.company-dropdown-wrapper').removeClass('.open');
    setTimeout(() => this._featureAuthService.reInitUserDetails.next(true), 500);
    // this._featureAuthService.bootboxInstance.next(bootbox);
  }

  ngOnDestroy() {
    jQuery('.modal-backdrop.fade.in').addClass('hide');
    jQuery('body').removeClass('hnjjkads modal-open');
  }

}
